import { getBlockType, unregisterBlockType } from '@wordpress/blocks';

(() => {
	const enabledAllBlocks = rtrbParams.enabled_blocks;

	const disableBlocks = Object.values(enabledAllBlocks).filter((block) => {
		return "false" == block.visibility;
	});

	disableBlocks.forEach((block) => {
		let blockNameSpace = "rtrb/".concat(block.value.replace(/_/g, "-"));
		(getBlockType(blockNameSpace) || "undefined" != getBlockType(blockNameSpace)) && (
			unregisterBlockType(blockNameSpace)
		);
	});

})();